'use client'

import { useState } from 'react'
import Link from 'next/link'
import { ShoppingCart, Menu, User, LogOut, X, Search } from 'lucide-react'
import { useAuth } from '@/context/AuthContext'
import { useCart } from '@/context/CartContext'
import AuthModal from '@/components/auth/AuthModal'

const categories = [
	'Oshxona jihozlari',
	'Tozalash texnikasi',
	'Isitish va sovutish',
	'Shaxsiy parvarish',
	'Aqlli uy',
]

export default function Navbar() {
	const { isLoggedIn, user, logout } = useAuth()
	const { totalCartCount } = useCart()
	const [isAuthModalOpen, setAuthModalOpen] = useState(false)
	const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)

	return (
		<>
			<AuthModal
				isOpen={isAuthModalOpen}
				onClose={() => setAuthModalOpen(false)}
			/>

			<header className='sticky top-0 z-50 w-full border-b border-gray-200 bg-white'>
				<div className='container mx-auto px-4'>
					<div className='h-16 flex items-center justify-between'>
						{/* Logo */}
						<Link href='/' className='text-2xl font-bold text-gray-900'>
							Tech House
						</Link>

						{/* Desktop Navigation */}
						<div className='hidden md:flex items-center gap-8 flex-1 mx-8'>
							<div className='flex gap-6'>
								{categories.map(cat => (
									<button
										key={cat}
										className='text-sm text-gray-600 hover:text-gray-900 transition'
									>
										{cat}
									</button>
								))}
							</div>
						</div>

						{/* Actions */}
						<div className='hidden md:flex items-center gap-6'>
							<div className='relative flex items-center'>
								<input
									type='text'
									placeholder='Qidirish...'
									className='px-3 py-2 text-sm border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 w-40'
								/>
								<Search className='absolute right-2.5 h-4 w-4 text-gray-400' />
							</div>

							<Link
								href='/cart'
								className='relative flex items-center gap-2 text-gray-600 hover:text-gray-900'
							>
								<ShoppingCart className='h-5 w-5' />
								{totalCartCount > 0 && (
									<span className='absolute -top-2 -right-2 bg-orange-500 text-white text-xs font-bold px-1.5 py-0.5 rounded-full'>
										{totalCartCount}
									</span>
								)}
							</Link>

							{isLoggedIn ? (
								<div className='flex items-center gap-3'>
									<span className='text-sm text-gray-700'>{user?.phone}</span>
									<button
										onClick={logout}
										className='flex items-center gap-2 text-red-600 hover:text-red-700 text-sm'
									>
										<LogOut className='h-4 w-4' />
										Chiqish
									</button>
								</div>
							) : (
								<button
									onClick={() => setAuthModalOpen(true)}
									className='flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition text-sm font-medium'
								>
									<User className='h-4 w-4' />
									Kirish
								</button>
							)}
						</div>

						{/* Mobile Menu Button */}
						<button
							className='md:hidden text-gray-600'
							onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
						>
							{isMobileMenuOpen ? (
								<X className='h-6 w-6' />
							) : (
								<Menu className='h-6 w-6' />
							)}
						</button>
					</div>

					{/* Mobile Menu */}
					{isMobileMenuOpen && (
						<div className='md:hidden border-t border-gray-200 py-4 space-y-4'>
							<div className='flex flex-col gap-2'>
								{categories.map(cat => (
									<button
										key={cat}
										className='text-left px-4 py-2 hover:bg-gray-50 rounded text-sm'
									>
										{cat}
									</button>
								))}
							</div>
							<div className='border-t pt-4'>
								<Link
									href='/cart'
									className='flex items-center gap-2 px-4 py-2 text-gray-700'
								>
									<ShoppingCart className='h-5 w-5' />
									Savat {totalCartCount > 0 && `(${totalCartCount})`}
								</Link>
							</div>
							{!isLoggedIn && (
								<button
									onClick={() => {
										setAuthModalOpen(true)
										setIsMobileMenuOpen(false)
									}}
									className='w-full mx-4 px-4 py-2 bg-blue-600 text-white rounded-lg text-sm font-medium'
								>
									Kirish
								</button>
							)}
						</div>
					)}
				</div>
			</header>
		</>
	)
}
