'use client'

import { useState, useEffect } from 'react'
import { Phone, Lock, Loader2, X } from 'lucide-react'
import { useAuth } from '@/context/AuthContext'

export default function AuthModal({ isOpen, onClose }) {
	const { login } = useAuth()
	const [step, setStep] = useState(1)
	const [phone, setPhone] = useState('+998')
	const [code, setCode] = useState('')
	const [timer, setTimer] = useState(120)
	const [error, setError] = useState('')
	const [loading, setLoading] = useState(false)

	useEffect(() => {
		if (!isOpen) {
			setStep(1)
			setPhone('+998')
			setCode('')
			setError('')
		}
	}, [isOpen])

	useEffect(() => {
		let interval
		if (step === 2 && timer > 0) {
			interval = setInterval(() => setTimer(prev => prev - 1), 1000)
		}
		return () => clearInterval(interval)
	}, [step, timer])

	const formatTime = seconds => {
		const m = Math.floor(seconds / 60)
		const s = seconds % 60
		return `${m}:${s < 10 ? '0' : ''}${s}`
	}

	const handlePhoneSubmit = e => {
		e.preventDefault()
		if (phone.length < 13) {
			setError("To'g'ri raqam kiriting")
			return
		}
		setLoading(true)
		setTimeout(() => {
			setLoading(false)
			setStep(2)
			setTimer(120)
			setError('')
		}, 1000)
	}

	const handleCodeSubmit = e => {
		e.preventDefault()
		if (code === '123456') {
			login(phone)
			onClose()
			setStep(1)
			setCode('')
			setPhone('+998')
		} else {
			setError("Kod noto'g'ri! Qaytadan urinib ko'ring.")
		}
	}

	if (!isOpen) return null

	return (
		<div className='fixed inset-0 z-50 flex items-center justify-center bg-black/50'>
			<div className='w-full max-w-sm bg-white rounded-2xl shadow-lg p-8'>
				<div className='flex items-center justify-between mb-6'>
					<h2 className='text-2xl font-bold text-gray-900'>
						{step === 1 ? 'Kirish' : 'Kodni kiriting'}
					</h2>
					<button
						onClick={onClose}
						className='text-gray-400 hover:text-gray-600'
					>
						<X className='h-6 w-6' />
					</button>
				</div>

				{step === 1 ? (
					<form onSubmit={handlePhoneSubmit} className='space-y-4'>
						<p className='text-sm text-gray-600 text-center'>
							Telefon raqamingizni kiriting va biz tasdiqlash kodini yuboramiz.
						</p>
						<div className='relative'>
							<Phone className='absolute left-3 top-3.5 h-5 w-5 text-gray-400' />
							<input
								type='tel'
								value={phone}
								onChange={e => setPhone(e.target.value)}
								className='w-full pl-10 pr-4 py-2.5 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500'
								placeholder='+998 90 123 45 67'
							/>
						</div>
						{error && (
							<p className='text-red-500 text-sm text-center'>{error}</p>
						)}
						<button
							type='submit'
							disabled={loading}
							className='w-full py-2.5 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg transition disabled:opacity-50 flex items-center justify-center gap-2'
						>
							{loading && <Loader2 className='h-4 w-4 animate-spin' />}
							Kod olish
						</button>
					</form>
				) : (
					<form onSubmit={handleCodeSubmit} className='space-y-4'>
						<p className='text-sm text-gray-600 text-center'>
							{phone} raqamiga SMS kod yuborildi.
						</p>
						<div className='relative'>
							<Lock className='absolute left-3 top-3.5 h-5 w-5 text-gray-400' />
							<input
								type='text'
								value={code}
								onChange={e => setCode(e.target.value)}
								className='w-full pl-10 pr-4 py-2.5 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-center tracking-widest'
								placeholder='000000'
								maxLength={6}
							/>
						</div>
						{error && (
							<p className='text-red-500 text-sm text-center'>{error}</p>
						)}
						<div className='text-center text-sm text-gray-600'>
							Qolgan vaqt:{' '}
							<span className='font-bold text-orange-600'>
								{formatTime(timer)}
							</span>
						</div>
						<button
							type='submit'
							className='w-full py-2.5 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg transition'
						>
							Tasdiqlash
						</button>
						<button
							type='button'
							onClick={() => setStep(1)}
							className='w-full py-2 text-blue-600 hover:text-blue-700 font-medium transition'
						>
							Raqamni o'zgartirish
						</button>
					</form>
				)}
			</div>
		</div>
	)
}
